// nav logo:hover (光澤效果)
.logo {
    width: 164px;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 50%;
        width: 20px;
        height: 100%;
        background: rgba(255, 255, 255, 0.5);
        transform: skewX(45deg) translateX(120px);
        transition: 0s;
    }

    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        width: 20px;
        height: 100%;
        background: rgba(255, 255, 255, 0.5);
        transform: skewX(45deg) translateX(-120px);
        transition: 0s;
    }

    &:hover::before {
        transform: skewX(45deg) translateX(-120px);
        transition: .5s;
    }

    &:hover:after {
        transform: skewX(45deg) translateX(120px);
        transition: .5s;
    }
}

